	module.exports = {"data":{"markdownRemark":{"headings":[],"html":"<p>Rumi is often regarded as the best selling poet in the United States. There’s a plethora of adultarated single-line translations lining bookstore shelves, recited at weddings, and shared over the internet. “The Rumi that people love is very beautiful in English, and the price you pay is to cut the culture and religion,” says Jawid Mojaddedi, a scholar of early Sufism and an excellent translator of Persian literature. This redesign of ‘The Masnavi, Book One’ takes the most faithful verse translation of the original text and complements the elegance of Rumi’s poetry with the richness of Islamic culture.</p>","frontmatter":{"title":"Rumi, The Masnavi","graduate":"Firas Kauchali","category":"Book Design","category2":null,"category3":null,"image":{"childImageSharp":{"sizes":{"base64":"data:image/jpeg;base64,","aspectRatio":1.5,"src":"/the-next-website/static/firas_rumi-76167599db52756979bdaf387d59ba0d-a7a62.jpg","srcSet":"/the-next-website/static/firas_rumi-76167599db52756979bdaf387d59ba0d-5a1a9.jpg 375w,\n/the-next-website/static/firas_rumi-76167599db52756979bdaf387d59ba0d-0908c.jpg 750w,\n/the-next-website/static/firas_rumi-76167599db52756979bdaf387d59ba0d-a7a62.jpg 1500w,\n/the-next-website/static/firas_rumi-76167599db52756979bdaf387d59ba0d-83d7d.jpg 2000w","sizes":"(max-width: 1500px) 100vw, 1500px"}}}}},"projectsInCategory":{"edges":[{"node":{"frontmatter":{"title":"Are We There Yet?","graduate":"Rana Soliman"}}},{"node":{"frontmatter":{"title":"Celestial","graduate":"Hannah Park"}}}]},"projectByStudent":{"edges":[{"node":{"frontmatter":{"title":"Regalis Display","graduate":"Firas Kauchali"}}}]}},"pathContext":{"slug":"/work/rumi-the-masnavi/","name":"/Rumi, The Masnavi/","title":"Rumi, The Masnavi","graduate":"/Firas Kauchali/","category":"/Book Design/"}}

